import React from "react";
import {useTranslation} from "react-i18next";
import {Canvas} from "@react-three/fiber";
import {OrbitControls} from "@react-three/drei";
import Model from "../../3DModels/Goe"

export default function WeAreDoing() {
    const {t} = useTranslation();

    return (
        <div id="FieldOfExpertise" className="py-32 md:mb-24 md:pt-0 min-h-screen flex items-center relative">
            <div className="md:container mx-4 md:mx-auto" style={{zIndex: '10'}}>
                <div className="grid grid-cols-1 gap-4 flex justify-center">
                    <div className="flex justify-center">
                        <h2 className="text-3xl md:text-5xl glitch text-center mb-0" data-glitch={t('field_of_expertise')}>
                            <span className="title-underline">
                                 {t('field_of_expertise')}
                            </span>
                        </h2>
                    </div>
                </div>
                <div className="grid grid-cols-1 lg:grid-cols-12">
                    <div itemScope itemType="https://schema.org/Organization" className="lg:col-span-6 grid grid-cols-1">
                        <h3 className="text-secondary mt-6 flex"><span className="my-auto css--connecting-line">01</span><span itemProp="keywords" className="text-white text-xl md:text-3xl">{t('e-commerce_dev')}</span></h3>
                        <h3 className="text-secondary mt-6 flex"><span className="my-auto css--connecting-line">02</span><span itemProp="keywords" className="text-white text-xl md:text-3xl">{t('saas_dev')}</span></h3>
                        <h3 className="text-secondary mt-6 flex"><span className="my-auto css--connecting-line">03</span><span itemProp="keywords" className="text-white text-xl md:text-3xl">{t('iot_and_custom')}</span></h3>
                        <h3 className="text-secondary mt-6 flex"><span className="my-auto css--connecting-line">04</span><span itemProp="keywords" className="text-white text-xl md:text-3xl">{t('mobile_apps')}</span></h3>
                        <h3 className="text-secondary mt-6 flex"><span className="my-auto css--connecting-line">05</span><span itemProp="keywords" className="text-white text-xl md:text-3xl">{t('websites_dev')}</span></h3>
                        <h3 className="text-secondary mt-6 flex"><span className="my-auto css--connecting-line">06</span><span itemProp="keywords" className="text-white text-xl md:text-3xl">{t('api_dev')}</span></h3>
                        <h3 className="text-secondary mt-6 flex"><span className="my-auto css--connecting-line">07</span><span itemProp="keywords" className="text-white text-xl md:text-3xl">{t('any_other')}</span></h3>
                    </div>
                    <div className="col-span-6 lg:block hidden">
                        <div style={{width: "100%", height: "100%", position: "relative"}}>
                            <Canvas
                                gl={{alpha: true}}
                                camera={{position: [0, 0, 5], fov: 40}}>
                                <OrbitControls makeDefault autoRotate enablePan={false} enableZoom={false} autoRotateSpeed={0.5} zoomSpeed={0.1}/>
                                <directionalLight intensity={1} position={[2, 2, 0]} color="#2fc7e5" distance={5} />
                                <React.Suspense fallback={null}>
                                    <Model/>
                                </React.Suspense>
                            </Canvas>
                        </div>
                    </div>
                </div>
            </div>
            <a href="#our-partners" className="css--weare-lines-parent md:grid hidden" style={{right: '5%'}}>
                <div className="css--weare-line-right">
                    <div className="css--weare-div-right">
                        <div className="css--weare-text-div bg-white text-black">{t('partners')}</div>
                    </div>
                </div>
            </a>
        </div>
    )
}


