import React, {useEffect, useLayoutEffect, useState} from "react";
import anime from "animejs";
import {useTranslation} from "react-i18next";


export default function HeroBlock() {
    const {t} = useTranslation();
    const [classState, setClassState] = useState('');

    useEffect(() => {
        setClassState('css--what-is-kyc-anim')
    }, [])
    useLayoutEffect(() => {
        let timeline = anime.timeline({
            easing: 'linear',
            autoplay: true,
        });
        timeline.add({
            targets: '#we-are-content',
            easing: 'easeOutExpo',
            opacity: [0, 1],
            scale: [0.8, 1],
            duration: 1000,
        });
        timeline.add({
            targets: 'canvas',
            easing: 'linear',
            opacity: [0, 1],
            scale: [0.5, 1],
            duration: 400,
        });
        timeline.add({
            targets: '.css--fadedtext',
            easing: 'easeOutExpo',
            opacity: [0, 1],
            translateX: ["-100%", 0],
            duration: 500
        });
    }, []);

    return (
        <div id="we-are" className="md:container mx-auto md:min-h-screen flex justify-center align-middle relative">
            <div className="grid grid-cols-1 lg:grid-cols-12 pt-32 md:pt-0">
                <div itemScope itemType="https://schema.org/Organization" id="we-are-content" className="lg:col-span-6 flex justify-center items-center flex-col py-12 lg:py-0">
                    <div className="flex flex-col justify-center relative text-center sm:text-left w-full sm:w-3/4 lg:w-auto">
                        <h1 className="text-3xl md:text-5xl glitch text-center mb-0" data-glitch={t('about_us')}>
                             <span className="title-underline">
                                {t('about_us')}
                             </span>
                        </h1>
                    </div>
                    <div itemProp="description" className="mt-10 px-4 xl:px-0 text-center text-white md:text-lg">{t('about_description')}</div>
                </div>
                <div className="col-span-6 flex justify-center css--we-are-hero">

                    <div className="w-2/3">
                        <div className={'css--what-is-kyc-triangle w-2/3 ' + classState} style={{minHeight: '15vh'}}>
                            <span/>
                            <span/>
                            <span/>
                            <span/>
                            <span/>
                        </div>
                    </div>
                </div>
            </div>
            <a href='#FieldOfExpertise' className="css--weare-lines-parent md:grid hidden" style={{left: '2%'}}>
                <div className="css--weare-line-left">
                    <div className="css--weare-div-left">
                        <div className="css--weare-text-div bg-white text-black">{t('what_we_do')}</div>
                    </div>
                </div>
            </a>
        </div>
    )
}
